import React from "react";




const ResultsList = props => (
    <div>
        {props.results.map(result => (
            <Result {...result} />
        ))}
    </div>
)

const Result = props => {
    return (
        <div style={{ margin: '1em' }}>
            <div>
                <div style={{ fontWeight: 'bold' }}>{props.value}</div>
                <div>{props.description}</div>
            </div>
        </div>
    )
}




export default Result


